body {
    font-family: poppins;
    background: #eee;
}

.circle-icon {
    width: 80px;
    height: 80px;
    border-radius: 100%;
    background: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.2em;
}

input, select {
    border: 0;
    border-radius: 3px;
    padding: 10px;
    outline: none;
    text-align: center;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    background: white;
}

button {
    border: 0;
    background: -webkit-linear-gradient(105deg, #5B86E5, rgb(44, 174, 185));
    background: linear-gradient(-15deg, #5B86E5, rgb(44, 174, 185));
    margin-top: 20px;
    padding: 12px 20px;
    color: white;
    border-radius: 3px;
}

header {
    width: 100%;
    padding: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 190px;
}

header .header-background {
    background: -webkit-linear-gradient(105deg, #5B86E5, #38CDDC);
    background: linear-gradient(-15deg, #5B86E5, #38CDDC);
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

header .header__inner {
    width: 100%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 15px;
    /* box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2); */
}

header .header__inner span {
    color: white;
    text-align: center;
}
header .header__inner span:nth-child(2) {
    margin-top: 5px;
}

header .header__inner span:nth-child(3) {
    font-size: 0.9em;
    opacity: 0.6;
}

header .header__inner input {
    width: 93%;
    position: absolute;
    font-size: 1.1em;
    bottom: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}
section#main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 0 30px;
}

section#main .currency-card-container {
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.currency-card {
    margin: 5px;
    display: flex;
    justify-content: space-between;
    background: white;
    border-radius: 3px;
    min-width: 250px;
    max-width: 100%;
    flex: 1 1;
    overflow: hidden;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

.currency-card .card-left {
    flex: 5 1;
    flex-direction: column;
    text-align: right;
}

.currency-card .card-left .currency-value,
.currency-card .card-left .currency-detail {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
}

.currency-card .card-left .currency-value {
    font-weight: bold;
    border-bottom: 1px solid #eee;
}

.currency-card .card-left .currency-detail {
    flex-direction: column;
    font-size: 0.9em;
}

.currency-card .btn-delete {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #CB3837;
    color: white;
    text-align: center;
    padding: 0 20px;
    cursor: pointer;
    -webkit-transition: all 250ms ease;
    transition: all 250ms ease;
    margin-top: 0;
}

.currency-card .btn-delete:hover {
    background: rgb(185, 48, 48);
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 250ms ease-in-out;
    transition: all 250ms ease-in-out;
}

.popup .popup-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    z-index: -1;
}

.popup .popup-inner {
    background: white;
    padding: 40px;
    border-radius: 5px;
    display: flex;
    width: 90%;
    flex-direction: column;
    align-items: center;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    -webkit-transition: all 250ms ease-in-out;
    transition: all 250ms ease-in-out;
}

.popup .popup-inner h3 {
    margin-bottom: 30px;
}

.popup .popup-inner form {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.popup.show {
    visibility: visible;
    opacity: 1;
}

.popup.show .popup-inner {
    visibility: visible;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.loading-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    -webkit-transition-delay: 1s;
            transition-delay: 1s;
    z-index: 15;
}

.loading-wrapper.show { visibility: visible; opacity: 1; }
.loading-wrapper.hide { visibility: hidden; opacity: 0; }

.loading-wrapper img {
    width: 80px;
    margin-top: -50px;
    margin-bottom: 10px;
}

.loading-wrapper span {
    font-size: 1.1em;
}
